.new-user-msg{
	text-align: center;
	font-size: 0;
	line-height: .81rem;

	.wrap{
		height: .81rem;
		position: relative;
		background: url(../images/newuserbg.jpg) repeat-x;
		background-size: auto .81rem;

		.left{
			width: .25rem;
			height: 100%;
			position: absolute;
			left : 0;
			top: 0;
			background: url(../images/newuserleft.jpg) no-repeat left top;
			background-size: auto .81rem;
		}

		.right{
			width: .25rem;
			height: 100%;
			position: absolute;
			right : 0;
			top: 0;
			background: url(../images/newuserright.jpg) no-repeat left top;
			background-size: auto .81rem;
		}
	
		.center{
			height: 100%;
		}

		p{
			display: inline-block;
			font-size: .25rem;
			height: .81rem;
			line-height: .86rem;
			padding-left: .42rem;
			position: relative;

			span{
				color: #c81522;
			}

			.gift{
				position: absolute;
				left: 0;
				top: 50%;
				@include translate(0, -50%);
				width: .23rem;
				height: .25rem;
				background: url(../images/gift.jpg) no-repeat left top;
				background-size: 100% 100%;
			}
		}
	}
}